// 登录

import { JSON_FETCH } from '@/axios/index';

// 登录
const login_api = `/login`;
// 退出登录
const logout_api = '/logout'
/**
 * 登录
 * @param { Object } params
 * @param { String } params.loginName 账号
 * @param { String } params.password 密码
 */
export function get_login_api(params) {
    return JSON_FETCH({
        method: 'post',
        url: login_api,
        data: params
    })
}

/**
 * 退出登录
 */
export function get_logout_api() {
    return JSON_FETCH({
        url: logout_api
    })
}

