// 系统管理

import { JSON_FETCH } from '@/axios/index';
/**
 * 角色
 */
// 删除角色
const role_delete_api = '/role/delete';
// 角色列表
const role_list_api = '/role/list';
// 添加角色
const role_save_api = '/role/save';
// 添加角色菜单
const role_saveMenu_api = '/role/saveMenuesByRoleId';
// 获取角色菜单
const role_getMenu_api = '/role/getMenuesByRoleId';
