import Cookies from "js-cookie";
import { get_login_api,get_logout_api } from '@/api/login';
const user = {
    state: {
        userInfo: null // 用户信息
    },
    mutations: {
        // 设置用户信息
        setUserInfo (state,info) {
            state.userInfo = info;
            Cookies.set('userInfo', info, {  expires: 1 });
		},
		// 清空用户信息
        setClearUserInfo(state) {
            state.userInfo = null;
            Cookies.remove('userInfo');
        }
    },
    actions: {
        // 获取用户信息
        getUserInfo ({ commit },params) {
            return new Promise((resolve, reject) => {
                get_login_api(params)
                  .then((res) => {
                    commit('setUserInfo', res);
                    resolve(res);
                  })
                  .catch((err) => {
                    reject(err)
                  })
              })
        },
        // 清空用户信息
        clearUserInfo ({ commit },params) {
            return new Promise((resolve, reject) => {
                get_logout_api(params)
                  .then((res) => {
                    commit('setClearUserInfo');
                    resolve(res);
                  })
                  .catch((err) => {
                    reject(err)
                  })
              })
        }
    }
};

export default user;
