import Vue from 'vue';
import Router from 'vue-router';
import Cookies from "js-cookie";
import { routers } from './routes';
import store from '@/store';
import { isToRoute } from '@/lib/utils';
Vue.use(Router)

const ROUTER = new Router({
	mode: 'history',
	base: "/piper/webmg",
	routes: routers
})

export default ROUTER
const WHITE_LIST = ['/login'];

ROUTER.beforeEach((to,from,next) => {
	const LOGIN_STATE = Cookies.get('userInfo');
	if ( LOGIN_STATE ) {
		if ( to.path === '/login' ) {
			next({ path: '/home' });
		} else {
			store.dispatch('getNavList', {userId:JSON.parse(LOGIN_STATE).userId}).then(res => {
				// 如果不在权限列表中禁止访问
				if ( !isToRoute(res,to) ) {
					next({path:'/NoAdmission'});
				// 如果在正常访问
				} else {
					next();
				}
				next();
			})
		}
	} else {
		if (WHITE_LIST.indexOf(to.path) !== -1) { // 在免登录白名单，直接进入
            next();
        } else {
            next('/login');
        }
	}
})
