/**
 * 简易版本，检测时候是对象.
 * @param item
 * @returns {boolean}
 */
export function isObject(item) {
	return item && typeof item === 'object' && !Array.isArray(item);
}
/**
 * 栏目格式化
 */
/**
 * 树状的算法
 * @param list 代转化数组
 * @param pid  起始节点
 */
export function getTrees(list, pid) {
	let items = {}
	// 获取每个节点的直属子节点，
	for (let i = 0; i < list.length; i++) {
		let key = list[i].parentId;
		if (items[key]) {
			items[key].push(list[i]) //把pid一样的放在一个数组里
		} else {
			items[key] = []
			items[key].push(list[i])
		}
	}
	return formatTree(items, pid)
  }
/**
 * 利用递归格式化每个节点
 */
function formatTree(items, pid) {
	let result = [];
	if (!items[pid]) {
		return result
	}
	for (let t of items[pid]) {
		t.children = formatTree(items, t.menuId);
		result.push(t);
	}
	return result;
  }
/**
 * 深度合并
 * @param { Object } target - 目标对象
 * @param { Array|Object }  sources - 源对象
 */
export function mergeDeep(target, ...sources) {
	if (!sources.length) return target;
	const source = sources.shift();

	if (isObject(target) && isObject(source)) {
		for (const key in source) {
			if (isObject(source[key])) {
				if (!target[key]) {
					Object.assign(target, {
						[key]: {}
					});
				}
				mergeDeep(target[key], source[key]);
			} else {
				Object.assign(target, {
					[key]: source[key]
				});
			}
		}
	}

	return mergeDeep(target, ...sources);
}
/**
 * 校验导航是否可以跳转
 * @param   { Array }  	list  列表数据
 * @param   { String }  to    要跳转的地址
 * 
 * @return  { Boolean }       返回boolean值
 */
export function isToRoute(list,to) {
	var flg = false;
	(function forList(list,to){
		list.forEach(ele => {
			if (ele.name == to.name) {
				flg = true;
			} else {
				if (ele.children && ele.children.length > 0) {
					forList(ele.children,to);
				}
			}
		})
	})(list,to)
	return flg;
}
/**
 * 格式化导航列表，访问列报表。
 *
 * @param   { Array }  routeList  前端定义的路由列表
 * @param   { Array }  list       后台返回该用户的导航数据
 *
 * @return  { Array }            返回一个格式化后的数据列表
 */
export function formatPermissionList(routeList,list) {
	let routerPermissionList = [];
	let navPermissionList = [];
	let new_list =  JSON.parse(JSON.stringify(list));
	/**
	 * @param  { Array } routeList  前端定义的路由列表
	 * @param  { Array } list       后台返回该用户的导航数据
	 * @param  { Array } parent     可访问列表
	 * @param  { Array } nav		导航展示列表
	 * @return { Array } [parent,nav] 返回俩个结构
	 */
	let formatData = (function fomartList(routeList,list,parent,nav) {
		routeList.forEach(routeItem => {
			// 防止children修改，修改数据源
			let routerItemData = JSON.parse(JSON.stringify(routeItem));
			let navItemData = JSON.parse(JSON.stringify(routeItem));
				routerItemData.children = [];
				navItemData.children = [];
			list.forEach(navItem => {
				if (routeItem.name == navItem.name) {
					if( !parent.children ) {
						parent.push(routerItemData);
						nav.push(navItemData)
					} else {
						if( nav.path != '/' ) {
							navItemData.path = nav.path + "/" +navItemData.path;
						}
						parent.children.push(routerItemData);
						nav.children.push(navItemData);
					}
				} 
				// 判断后台返回数据是否有子菜单，前端是否定义
				if( routeItem.children && routeItem.children.length > 0 && navItem.children && navItem.children.length > 0 ) {
					fomartList(routeItem.children,navItem.children,routerItemData,navItemData);
				}
			});
			// TODO: 判断后台列表是否有子集，前端定义路由是否有子集
			if( !list.children && routeItem.children && routeItem.children.length > 0 ){
				routeItem.children.forEach(ele => {
					// 循环子集判断是否为增删改查页面
					if( ele.type == 1 ) {
						(function isAdd(parent) {
							if (parent instanceof Array) {
								parent.forEach(item => {
									if ( item.name === routeItem.name ) {
										item.children.push(ele);
									}
								});
							} else {
								isAdd(parent.children);
							}
						})(parent)
					}
				});
			}
		});
		/**
		 * @return  {[type]}  统一返回数组
		 */
		return {
			parent: parent instanceof Array ? parent : [parent],
			nav: nav instanceof Array ? nav : [nav]
		}
	})(routeList,new_list,routerPermissionList,navPermissionList);
	return formatData;
}
/**
 * 获取相同数据
 * @param   { Array }  roleMenuList  角色菜单
 * @param   { Array }  allMenuList   所有菜单
 * 
 * @return  { Array }  相同的放在数组中
 */
export function getSame(roleMenuList, allMenuList) {
	if ( roleMenuList.length > 0) {
		let obj = {};
		let arr = [];
		let new_arr = roleMenuList.concat(allMenuList);
		new_arr.forEach(ele => {
			if( !obj[ele.menuId] ) {
				obj[ele.menuId] = 1;
			} else {
				if ( ele.isLeaf == 1 ) {
					arr.push(ele.menuId);
				}
			}
		});
		return arr;
	} else {
		return [];
	}
}