import Axios from 'axios'
import Cookies from 'js-cookie'
import {
    Message
} from 'element-ui'

// 全局通用默认配置
const defaultConfig = {
    timeout: 3000
}
/**
 * @description 请求class
 */
class httpRequest {
    constructor(opt) {
        this.options = Object.assign({ method: 'get' }, opt )
        // 存储请求队列
        this.queue = {}
    }
    // 销毁请求实例
    destroy(url) {
        delete this.queue[url]
        const queue = Object.keys(this.queue)
        return queue.length
    }
    // 请求拦截
    interceptors(instance, url) {
        // 全局添加请求拦截器
        instance.interceptors.request.use(
            (config) => {
                // 判断是否为get请求
                if (config.method.toLowerCase() == 'get' && typeof config.params === 'string') {
                    config.url = config.url + '?' + config.params
                    delete config.params
                }
                // 判断是否为form表单
                if (config.form_request) {
                    // config.data = querystring.stringify(config.data)
                    delete config.form_request
                }
                // // 权限header信息
                let data = Cookies.get('userInfo')
                if (data) {
                    let objData = JSON.parse(data)
                    config.headers = Object.assign({}, config.headers, {
                        'Authorization': `${objData.loginName}_${objData.token}`
                    })
                }
                return config
            },
            (error) => {
                // 对请求错误做些什么
                return Promise.reject(error)
            }
        )

        // 添加响应拦截器
        instance.interceptors.response.use(
            (res) => {
                let { data } = res;
                this.destroy(url);
                if ( res.status >= 200 && res.status < 300 ) {
                    if (res.data.code == 200) {
                        return data.data;
                    }
                    if ( res.data.code == 500 ) {
                        // DEBUG: 测试时，注释掉下面两行
                        // location.replace(backUrl('/login'));
                        Message.error(res.data.msg || '服务内部错误');
                        return Promise.reject(res);
                    }
                    return data;
                } else {
                    return Promise.reject(res);
                }
            },
            (error) => {
                Message.error('服务内部错误');
                // 对响应错误做点什么
                return Promise.reject(error)
            }
        )
        return instance
    }
    // 创建实例
    create() {
        let conf = Object.assign({}, defaultConfig, this.options);
        return Axios.create(conf);
    }
    // 请求实例
    request(options) {
        let instance = this.create();
        this.interceptors(instance, options.url);
        options = Object.assign({}, options);
        this.queue[options.url] = instance;
        return instance(options);
    }
}
export default httpRequest