// 首页
const Main = () => import('@/views/Main');
// 登录
const Login = () => import('@/views/login');
// 管理员列表页
const AdminList = () => import('@/views/admin/list');
// 出版社列表页
const PublisherList = () => import('@/views/publisher/list');
// 权限控制禁止访问页面
const NoAdmission = () => import('@/views/No-admission');
// 订阅
const Subcription = () => import('@/views/publisher/subcription');
// 推荐出版社
// const RecommendPublisherList = () => import('@/views/recommend/publisher-list.vue');
// 推荐出版社
const Content = () => import('@/views/publisher/content.vue');
// 推荐内容
// const RecommendContentList = () => import('@/views/recommend/content-list.vue');
// 系统管理：角色管理
const Role = () => import('@/views/system/role.vue');
// 系统管理：角色菜单
const RoleMenu = () => import('@/views/system/role-menu.vue');
// 系统管理：用户管理
const User = () => import('@/views/system/user.vue');
// 系统管理：用户角色
const UserRole = () => import('@/views/system/user-role.vue');
// 系统管理：菜单管理
const Menu = () => import('@/views/system/menu.vue');
// 首页
const Home = () => import('@/views/home');
//  修改密码
const UpdatePwd = () => import('@/views/login/update-pwd.vue')
// 路由列表
let otherRouter = {
    path: "/",
    component: Main,
    redirect: '/home',
    children: [],
};
const routeList = [
    {
        path: '/',
        name : '系统设置',
        children: [
            {
                path: '/role',
                name: "角色管理",
                component:Role,
            },
            {
                path: '/menu',
                name: "菜单管理",
                component:Menu,
            },
            {
                path: '/user',
                name: "用户管理",
                component:User,
            },
            {
                path: "/roleMenu/:id",
                component: RoleMenu,
                type:1,
                name: '角色菜单',
                meta: {
                    parentPath: '/role'
                }
            },
            {
                path: "/userRole/:id",
                component: UserRole,
                type:1,
                name: '用户角色',
                meta: {
                    parentPath: '/user'
                }
            }
        ]
    },
    {
        path: '/',
        name: 'piper管理',
        children: [
            {
                path: '/adminList',
                name: "邮件组管理员",
                component:AdminList,
            },
            {
                name: '出版社管理',
                path: '/publisher',
                component: PublisherList
            },
            {
                path: "/subcription/:id",
                component: Subcription,
                type:1,
                name: '订阅',
                meta: {
                    parentPath: '/publisher'
                }
            },
            {
                type:1,
                meta: {
                    parentPath: '/publisher'
                },
                path: '/content/:id',
                name: "内容",
                component:Content,
            },
        ]
    }
]

const routes = [
    {
        name: 'login',
        path: '/login',
        component: Login
    }
]
const NoAdmissionRoute = [
    {
        name: 'NoAdmission',
        path: '/NoAdmission',
        component: NoAdmission
    },
    {
        path: "/",
        component: Main,
        children: [
            {
                name: '首页',
                path: '/home',
                component: Home
            },
            {
                name: '更新密码',
                path: '/update',
                component: UpdatePwd
            }
        ],
    }
    
]
routeList.forEach(ele => {
    if (ele.children && ele.children.length > 0) {
        otherRouter.children.push(...ele.children);
    } else {
        otherRouter.children.push(ele);
    }
});
export { NoAdmissionRoute };
export { routeList };
export const routers = [otherRouter , ...NoAdmissionRoute, ...routes];
