import { NoAdmissionRoute,routeList } from '@/router/routes';
import { formatPermissionList } from '@/lib/utils.js';
import { get_user_menus_api } from '@/api/system';
import { getTrees } from '@/lib/utils.js'
const nav = {
    state: {
        navList: [] // 导航数据
    },
    mutations: {
        // 设置用户信息
        setNavList (state,list) {
            state.navList = list;
        }
    },
    actions: {
        // 获取用户信息
        getNavList ({commit}, params) {
            return new Promise((resolve, reject) => {
                get_user_menus_api(params)
                .then(res => {
                    let newArr = []
                    let obj = formatPermissionList(routeList,getTrees(res,-1));
                    newArr = obj.parent;
                    newArr.push(...NoAdmissionRoute);
                    commit('setNavList', obj.nav);
                    resolve(newArr);
                })
                .catch(err => {
                    reject(err)
                })
            })
        }
    }
};

export default nav;
