/**
 * @description
 * - 实例化请求对象,可以针对不同的情况，实例化多个对象
 * - 例如：真针对不同域名的处理，可以初始化多个不同域名（baseURL）的对象
 */
import config from '@/config.js'
import { mergeDeep } from '@/lib/utils'
import HttpRequest from './base-config';
let default_conf = {
  baseURL: config.url
}
// form表单请求
let form_conf = mergeDeep({}, default_conf, {
  headers: {
    'Content-Type': 'application/x-www-form-urlencoded'
  }
})
const JSON_OBJ = new HttpRequest(default_conf)
const FORM_OBJ = new HttpRequest(form_conf)
const JSON_FETCH = JSON_OBJ.request.bind(JSON_OBJ)
const FORM_FETCH= FORM_OBJ.request.bind(FORM_OBJ)

export { JSON_FETCH, FORM_FETCH }
export default JSON_FETCH
